/**
 * Gera uma cor correspondente ao status do veculo com base nos evetos de movimentao do veculo
 * @param {Object} veiculo - O objeto que representa o veculo.
 * @returns {string} A cor correspondente ao status do veculo.
 */
export function getColorByVehicleStatus(veiculo) {
  if (veiculo.ChegadaBaseDT) {
    return '#0099ff' //azul para veiculso na base
  }
  if (veiculo.RetornoDestinoDT) {
    return '#32a81d' //verde para veiculos livres, mas em trnsito
  }
  return '#ffcc00' //amarelo para ocupado [todos os demais eventos de movimentao]
}